























import { Component, Vue, Prop } from 'vue-property-decorator';

@Component<PuzzleCalculatorPad>({
  components: {},
})
export default class PuzzleCalculatorPad extends Vue {
  @Prop({ required: true, type: String })
  value!: string;
  get valueProxy() {
    return this.value;
  }
  set valueProxy(v: string) {
    if (!/^[0-9+\-*]*$/.test(v)) {
      return;
    }
    this.$emit('input', v);
  }
}
